@import url(https://fonts.googleapis.com/css?family=Passion+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.masthead {
    height: 60vh;
    background-image: url("/images/couple-date-lens-flare-40525.jpg");
    color: white;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  #about {
    box-shadow: 5px 5px 5px rgb(197, 197, 197);
  }

  .benefits { 
    display: table; 
    margin: 0 auto;
  }
.services {
    font-size: 50px;
    font-weight: 100;
    padding-left: 11px;
    border-bottom: 1px solid rgb(230, 230, 230);
}

.contact {
    font-size: 44px;
    font-weight: 100;
    padding-left: 11px;
    border-bottom: 1px solid rgb(230, 230, 230);
}
#what {
    box-shadow: 5px 5px 5px rgb(197, 197, 197);
  }

.happy{
  width: 100%;
}
  p {
    line-height: 1.75;
  }
  
  .text-faded {
    color: rgba(255, 255, 255, 0.3);
  }
  
  .site-heading {
    margin-top: 5rem;
    margin-bottom: 5rem;
    text-transform: uppercase;
    line-height: 1;
    font-family: 'Raleway';
  }
  
  .site-heading .site-heading-upper {
    display: block;
    font-size: 2rem;
    font-weight: 800;
  }
  
  .site-heading .site-heading-lower {
    font-size: 5rem;
    font-weight: 100;
    line-height: 4rem;
  }
  
  .page-section {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  
  .section-heading {
    text-transform: uppercase;
  }
  
  .section-heading .section-heading-upper {
    display: block;
    font-size: 1rem;
    font-weight: 800;
  }
  
  .section-heading .section-heading-lower {
    display: block;
    font-size: 3rem;
    font-weight: 100;
  }
  
  .bg-faded {
    background-color: rgb(140, 114, 114, .2);
  }

  .product-item-img {
    box-shadow: 5px 5px 5px rgb(197, 197, 197);
  }

  @media (min-width: 992px) {
    .product-item .product-item-title {
      position: relative;
      z-index: 1;
      margin-bottom: -3rem;
    }
    .product-item .product-item-img {
      position: relative;
      z-index: 0;
      max-width: 60vw;
    }
    .product-item .product-item-description {
      position: relative;
      z-index: 1;
      margin-top: -3rem;
      max-width: 50vw;
    }
  }

.float {
    position:fixed;
    width:60px;
    height:60px;
    bottom:40px;
    right:40px;
    background-color:orange;
    opacity: .5;
    color:#FFF;
    border-radius:50px;
    text-align:center;
    box-shadow: 2px 2px 3px #999;
    }

.my-float {
        margin-top: 15px;
    }
